<template>
  <div>
    <div class="side_box">
      <div class="team_box" style="background-color: rgba(255, 0, 0, 0);">
        <div style="float:left; padding: 10px;">
          <img src="이미지경로" width="100" height="100">
        </div>
        <div class="user-info">{{$store.name}}</div>
        <div class="user-info">컴퓨터공학과</div>
        <div class="user-info">{{$store.email}}</div>
        <a class="user-info" href="">My page</a>
        <a class="user-info" href="" @click.prevent="logout">Logout</a>
      </div>
      <hr style="border: solid 2px #A1C1F8; margin: 20px; margin-left: 10px; margin-right: 10px;">
      <div class="team_box">
        <div class="home-title">My Project Team</div>
        <hr style="border: solid 2px #6281B8; margin: 0px;">
        <div class="content">ProofU</div>
        <hr style="border: solid 0.5px black; margin: 0px; margin-left: 5px; margin-right: 5px">
        <div class="content">영어 스터디</div>
        <hr style="border: solid 0.5px black; margin: 0px; margin-left: 5px; margin-right: 5px">
        <div class="content">취업 스터디</div>
        <hr style="border: solid 0.5px black; margin: 0px; margin-left: 5px; margin-right: 5px">
        <div class="content">동아리</div>
        <hr style="border: solid 0.5px black; margin: 0px; margin-left: 5px; margin-right: 5px">
        <div class="content">P&C</div>
        <hr style="border: solid 0.5px black; margin: 0px; margin-left: 5px; margin-right: 5px">
        <div class="home-title" style="margin-left: 10px;"> &plus; Create Team</div>
      </div>
    </div>
    <div class="contain">
      <div class="team_box">
        <div class="home-title">Project / Major</div>
        <hr style="border: solid 2px #6281B8; margin: 0px;">
        <div class="content">ProofU / 컴퓨터공학과</div>
        <hr style="border: solid 0.5px black; margin: 0px; margin-left: 5px; margin-right: 5px">
        <div class="content">태양열전지 / 전기공학과</div>
        <hr style="border: solid 0.5px black; margin: 0px; margin-left: 5px; margin-right: 5px">
        <div class="content">SLAMBot / 전자공학과</div>
        <hr style="border: solid 0.5px black; margin: 0px; margin-left: 5px; margin-right: 5px">
        <div class="content">ProofU / 컴퓨터공학과</div>
        <hr style="border: solid 0.5px black; margin: 0px; margin-left: 5px; margin-right: 5px">
        <div class="content">태양열전지 / 전기공학과</div>
        <hr style="border: solid 0.5px black; margin: 0px; margin-left: 5px; margin-right: 5px">
        <div class="content">SLAMBot / 전자공학과</div>
      </div>
      <div class="team_box">
        <div class="home-title">Personal Boards</div>
        <hr style="border: solid 2px #6281B8; margin: 0px;">
        <div class="board-list" ref="boardList">
          <div class="board-item" v-for="b in boards" :key="b.id"
            :data-bgcolor="b.bgColor" ref="boardItem">
            <router-link :to="`/b/${b.id}`">
              <div class="board-item-title">{{b.title}}</div>
            </router-link>
          </div>
          <div class="board-item board-item-new">
            <a class="new-board-btn" href="" @click.prevent="SET_IS_ADD_BOARD(true)">
              Create new board...
            </a>
          </div>
        </div>
        <AddBoard v-if="isAddBoard" />
      </div>
    </div>
    <div class="side_box">
      <div class="team_box">
        <div class="home-title">Study</div>
        <hr style="border: solid 2px #6281B8; margin: 0px;">
      </div>
      <div class="team_box">
        <div class="home-title">YU notice</div>
        <hr style="border: solid 2px #6281B8; margin: 0px;">
      </div>
    </div>
  </div>
</template>

<script>
import {mapState, mapMutations, mapActions} from 'vuex'
import AddBoard from './AddBoard.vue'

export default {
  components: {
    AddBoard
  },
  data() {
    return {
      loading: false,
      error: ''
    }
  },
  computed: {
    ...mapState({
      isAddBoard: 'isAddBoard',
      boards: 'boards'
    })
  },
  created() {
    this.fetchData()
    this.SET_THEME()
  },
  updated() {
    this.$refs.boardItem.forEach(el => {
      el.style.backgroundColor = el.dataset.bgcolor
    })
  },
  methods: {
    ...mapMutations([
      'SET_IS_ADD_BOARD',
      'SET_THEME',
      'LOGOUT'
    ]),
    ...mapActions([
      'FETCH_BOARDS'
    ]),
    fetchData() {
      this.loading = true
      this.FETCH_BOARDS().finally(_=> {
        this.loading = false
      })
    },
    logout() {
      this.LOGOUT()
      this.$router.push('/login')
    }
  }
}
</script>

<style>
.side_box {
  width:20%;
  height:auto;
  float:left;
  margin:5px;
}
.contain{
  width:50%;
  height:auto;
  float:left;
  margin:5px;
}
.team_box{
  background-color:white;
  margin:10px;
}
.content {
  padding: 5px;
  padding-left: 20px;
  font-size: 15px;
}
.user-info {
  padding: 5px;
  font-size: 18px;
  color: white;
  font-weight: bold;
}
.home-title {
  padding: 10px;
  font-size: 18px;
  font-weight: bold;
}
.board-list {
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
}
.board-item {
  width: 48%;
  height: 200px;
  margin: 0 1% 20px 1%;
  border-radius: 10px;
}
.board-item-new {
  background-color: #ddd;
}
.board-item a {
  text-decoration: none;
  display: block;
  width:100%;
  height: 100%;
}
.board-item a:hover,
.board-item a:focus {
  background-color: rgba(0,0,0, .1);
  color: #666;
  border-radius: 10px;
}
.board-item-title {
  color: #fff;
  font-size: 20px;
  font-weight: 700;
  padding: 20px;
}
.board-item a.new-board-btn {
  display: table-cell;
  vertical-align: middle;
  text-align: center;
  height: inherit;
  width: 1%;
  color: #888;
  font-weight: 700;
}
</style>



